import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { CitaDisponible } from 'src/app/entities/cita-disponible';
import { AsesoriaSstAgendamientoVivoPage } from 'src/app/pages/asesoria-sst-agendamiento-vivo/asesoria-sst-agendamiento-vivo.page';

@Component({
  selector: 'app-cita-item-accordion',
  templateUrl: './cita-item-accordion.component.html',
  styleUrls: ['./cita-item-accordion.component.scss'],
})
export class CitaItemAccordionComponent implements OnInit {
  @Input() citaDisponible: CitaDisponible;

  @Output() horaSeleccionada = new EventEmitter<number>();

  isHiddenAccordion: boolean;
  hourSelected: number;

  constructor(
    private compPadre: AsesoriaSstAgendamientoVivoPage
  ) {
    this.compPadre.refrescarAgendaSubject.subscribe((pId: number) => {
      this.hourSelected = pId;
    });
  }

  ngOnInit() {
    this.isHiddenAccordion = false;
    this.hourSelected = 0;
  }

  onClickHiddenAccordion(hide) {
    this.isHiddenAccordion = hide;
  }

  onClickHora(pId: number) {
    this.horaSeleccionada.emit(pId);
  }

}
