import { Entity } from './entity';

export class CitaHoraDisponible extends Entity {
  get hora(): string {
    return this._hora;
  }

  set hora(hora: string) {
    this._hora = hora;
  }

  private _hora: string;

  static parse(json: any[]) {
    return json.map((item: any) => new CitaHoraDisponible(item));
  }

  constructor(json: any) {
    super(json.Pk_Id_ExpertoTecnicoAgenda);
    this._hora = json.strHoraCita;
  }
}
