import { Entity } from './entity';
import { ExpertoTecnico } from './experto-tecnico';
import { TemaAsesoria } from './tema-asesoria';

export class AsesoriaSst extends Entity {
  get idEmpresa(): number {
    return this._idEmpresa;
  }

  set idEmpresa(idEmpresa: number) {
    this._idEmpresa = idEmpresa;
  }

  get idTrabajador(): number {
    return this._idTrabajador;
  }

  set idTrabajador(idTrabajador: number) {
    this._idTrabajador = idTrabajador;
  }

  get contacto(): string {
    return this._contacto;
  }

  set contacto(contacto: string) {
    this._contacto = contacto;
  }

  get indicativo(): number {
    return this._indicativo;
  }

  set indicativo(indicativo: number) {
    this._indicativo = indicativo;
  }

  get numeroContacto(): number {
    return this._numeroContacto;
  }

  set numeroContacto(numeroContacto: number) {
    this._numeroContacto = numeroContacto;
  }

  get idTema(): number {
    return this._idTema;
  }

  set idTema(idTema: number) {
    this._idTema = idTema;
  }

  get temaAsesoria(): TemaAsesoria {
    return this._temaAsesoria;
  }

  set temaAsesoria(temaAsesoria: TemaAsesoria) {
    this._temaAsesoria = temaAsesoria;
  }

  get fecha(): string {
    return this._fecha;
  }

  set fecha(fecha: string) {
    this._fecha = fecha;
  }

  get hora(): string {
    return this._hora;
  }

  set hora(hora: string) {
    this._hora = hora;
  }

  get canalAsistencia(): string {
    return this._canalAsistencia;
  }

  set canalAsistencia(canalAsistencia: string) {
    this._canalAsistencia = canalAsistencia;
  }

  get idExpertoTecnico(): number {
    return this._idExpertoTecnico;
  }

  set idExpertoTecnico(idExpertoTecnico: number) {
    this._idExpertoTecnico = idExpertoTecnico;
  }

  get expertoTecnico(): ExpertoTecnico {
    return this._expertoTecnico;
  }

  set expertoTecnico(expertoTecnico: ExpertoTecnico) {
    this._expertoTecnico = expertoTecnico;
  }

  private _idEmpresa: number;

  private _idTrabajador: number;

  private _contacto: string;

  private _indicativo: number;

  private _numeroContacto: number;

  private _idTema: number;

  private _temaAsesoria: TemaAsesoria;

  private _fecha: string;

  private _hora: string;

  private _canalAsistencia: string;

  private _idExpertoTecnico: number;

  private _expertoTecnico: ExpertoTecnico;

  constructor(id = 0) {
    super(id);
  }

  static parse(json: any[]) {
    return json.map((item: any) => {
      const asesoria = new AsesoriaSst(item.id);
      const jsonTema = item.tema;

      asesoria.temaAsesoria = new TemaAsesoria(jsonTema);
      asesoria.fecha = item.fecha;
      asesoria.idTema = item.idTema;
      asesoria.idExpertoTecnico = item.idExpertoTecnico;

      return asesoria;
    });
  }

  toUrlSearchParams(): URLSearchParams {
    const params: URLSearchParams = new URLSearchParams();
    params.append('contacto', this._contacto);
    params.append('indicativo', String(this._indicativo));
    params.append('numeroContacto', String(this._numeroContacto));
    params.append('idTemaAsesoria', String(this._temaAsesoria.id));
    return params;
  }

  toJson(): string {
    return JSON.stringify({
      nitEmpresa: String(this.idEmpresa),
      cedulaQuienReporta: String(this.idTrabajador),
      contacto: this._contacto,
      indicativo: this._indicativo,
      numeroContacto: this._numeroContacto,
      temaAsesoria: String(this._temaAsesoria.id),
      fecha: this._fecha,
      hora: this._hora,
      canalAsistencia: this._canalAsistencia,
      expertoTecnico: String(this._expertoTecnico.id),
    });
  }
}
