import { CitaHoraDisponible } from './cita-hora-disponible';

export class CitaDisponible {
  get fecha(): string {
    return this._fecha;
  }

  set fecha(fecha: string) {
    this._fecha = fecha;
  }

  get fechaTexto(): string {
    return this._fechaTexto;
  }

  set fechaTexto(fechaTexto: string) {
    this._fechaTexto = fechaTexto;
  }

  get citas(): CitaHoraDisponible[] {
    return this._citas;
  }

  set citas(citas: CitaHoraDisponible[]) {
    this._citas = citas;
  }

  get mensaje(): string {
    return this._mensaje;
  }

  set mensaje(mensaje: string) {
    this._mensaje = mensaje;
  }

  private _fecha: string;
  private _fechaTexto: string;
  private _citas: CitaHoraDisponible[];
  private _mensaje: string;

  static parse(json: any[], fechaSeleccionada: string) {
    return json.map((item: any) => new CitaDisponible(item, fechaSeleccionada));
  }

  constructor(json: any, fechaSeleccionada: string) {
    if (json.edRespuestaServicio !== null && json.edRespuestaServicio.intRespuesta === 3) {
      this._mensaje = json.edRespuestaServicio.strRespuesta;
      this._fecha = fechaSeleccionada;
    } else {
      const tFecha = json.Dtm_FechaCita as string;
      this._fecha = tFecha.replace('T00:00:00', '');
      this._citas = CitaHoraDisponible.parse(json.listaHorasFecha);
      this.mensaje = '';
    }

    const date = new Date(this._fecha + 'T00:00:00-05:00');
    const options = { year: 'numeric', month: 'long', day: 'numeric' } as any;
    this._fechaTexto = date.toLocaleDateString('es-ES', options).replace(/ de /g, ' ');
  }
}
